// export default LifeCounter

class LifeCounter extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      playerOne : [{name : "NoiseBox"}],
      playerTwo : "Tubstrr"

    };
  }


  render() {
    return (
      <div>
        <h1>{this.state.playerOne[1]}</h1>
      </div>
    );
  }
}

export default LifeCounter



